const errorPage = () => (
  <div className={'error-page'}>
    <h1>404 - Page Not Found.</h1>
    <style jsx>
      {
        `
        * {
          margin: 0;
          padding: 0;
        }
        .error-page {
          width: 100%;
          height: 100%;
          background-color: #e9819b;
        }
        h1 {
          color: #4055b1;
        }
      `
      }
    </style>
  </div >
)

export default errorPage